#myBtn, #myBtn-registr {
  color: #fff;
  cursor: pointer;
  width: 180px;
  background-color: #9c27b0;
  border: none;
  padding: 12px 16px;
  font-size: 18px;
  font-weight: bold;
}

.modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .4);
  padding-top: 100px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  width: 50%;
  background-color: #e1bee7;
  border: 5px solid #7b1fa2;
  margin: auto;
  padding: 0;
  animation-name: animatetop;
  animation-duration: .4s;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2), 0 6px 20px rgba(0, 0, 0, .19);
}

@keyframes animatetop {
  from {
    opacity: 0;
    top: -300px;
  }

  to {
    opacity: 1;
    top: 0;
  }
}

.close {
  color: #fff;
  float: right;
  font-size: 36px;
  font-weight: bold;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.closeSign {
  color: #fff;
  float: right;
  font-size: 36px;
  font-weight: bold;
}

.closeSign:hover, .closeSign:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.modal-header {
  color: #fff;
  text-align: center;
  background-color: #9c27b0;
  padding: 2px 16px;
}

.modal-body {
  padding: 2px 16px;
}

.modal-footer {
  color: #fff;
  text-align: center;
  background-color: #9c27b0;
  padding: 2px 16px;
}

